.profileContainer {
    color: white;
    margin: 0;
    width: 100%;
    height: 789px;
    font-family: "Author-Bold";
    background-size: cover;
}

.pdf-button {
    display: inline-block;
    padding: 10px 20px;
    font-size: 16px;
    font-weight: bold;
    text-decoration: none;
    color: #fff;
    background-color: #007bff;
    border: none;
    border-radius: 4px;
    cursor: pointer;
  }
  
  .pdf-button:hover {
    background-color: #0069d9;
  }