/**
* This is a variable font
* You can controll variable axes as shown below:
* font-variation-settings: 'wght' 700.0;
*
* available axes:

* 'wght' (range from 200.0 to 700.0)

*/

@font-face {
    font-family: 'Author-Variable';
    src: url('../public/fonts/Author-Variable.woff2') format('woff2'),
         url('../public/fonts/Author-Variable.woff') format('woff'),
         url('../public/fonts/Author-Variable.ttf') format('truetype');
         font-weight: 200 700;
         font-display: swap;
         font-style: normal;
  }
  
  
